<template>
  <!-- -->
  <el-card class="editbox">
    <!--  -->
    <div id="toolbar-container"></div>
    <!--  -->
    <div id="editor"></div>
    <el-button type="primary" @click="showTodos">Todos</el-button>
    <el-button type="primary" @click="submitArticle"></el-button>
    <el-button type="primary" @click="getArticle"></el-button>
    <div v-html="content"></div>
  </el-card>
</template>
<script>
import { ref, reactive, onMounted } from "vue"
import { useStore } from "vuex"
import Editor from "@ckeditor/ckeditor5-build-decoupled-document"
// import CodeBlock from '@ckeditor/ckeditor5-code-block/src/codeblock.js';
import MyUploadAdapter from "./uploadImg"

export default {
  name: "CKEditor",
  setup() {
    // 
    let editorObj = reactive({})
    // 
    const initCKEditor = () => {
      Editor.create(document.querySelector("#editor"), {
        language: "zh-cn",
        // plugins: [ CodeBlock ],
        // toolbar: [ 'codeBlock' ]
      })
        .then((editor) => {
          const toolbarContainer = document.querySelector("#toolbar-container")
          toolbarContainer.appendChild(editor.ui.view.toolbar.element)
          editorObj = editor //
          editor.plugins.get("FileRepository").createUploadAdapter = (loader) => {
            return new MyUploadAdapter(loader) //
          }
        })
        .catch((error) => {
          console.error(error)
        })
    }
    // 
    const getEditorData = () => {
      return editorObj.getData()
    }
    // 
    const setEditorData = (data) => {
      return editorObj.setData(data)
    }
    //mounted
    onMounted(() => {
      initCKEditor()
    })
    // 
    let content = ref("")
    // Vuexstore
    const store = useStore()
    const showTodos = () => {
      setEditorData(
        JSON.stringify(
          store.state.test.todos.reduce((res, todo) => (res += todo.title + "<br/>"), "")
        )
      )
      console.log(" =", getEditorData().length)
    }
    const submitArticle = () => {
      store.commit("setArticle", getEditorData())
      content.value = getEditorData() //
    }
    const getArticle = () => {
      setEditorData(store.state.test.article)
    }
    return {
      editorObj,
      getEditorData,
      setEditorData,
      showTodos,
      submitArticle,
      getArticle,
      content,
    }
  },
}
</script>
<style scope>
/*  */
/* @import url("./content-styles.css"); */
.editbox {
  width: 1250px;
  height: 1000px;
  margin: 0 auto; /**/
  padding-bottom: 15px;
}
#editor {
  border: 2px, solid, rgb(184, 179, 179);
  background-color: aliceblue;
  height: 350px;
  margin-bottom: 10px;
}
</style>
